    musicId: false, // id
    playList: [], // 
    playIndex: 0, // index
    playTypeIndex: 0, //  0: 1: 2: 3:

    audio: {
        type: false, //  true: Data
        time: false, // 
        skip: false, //  
        currentTime: 0, //
        duration: 0, // 
        volume: 0, // 
    },

    videoId: "", // id

    showRecord: false, // 
