// localStorage 
let storage = {
    get: function (key) {
        var value = localStorage.getItem(key);
        if (value) {
            try {
                var value_json = JSON.parse(value);
                if (typeof value_json === 'object') {
                    return value_json;
                } else if (typeof value_json === 'number') {
                    return value_json;
                }
            } catch (e) {
                return value;
            }
        } else {
            return false;
        }
    },
    set: function (key, value) {
        localStorage.setItem(key, value);
    },
    remove: function (key) {
        localStorage.removeItem(key);
    },
    clear: function () {
        localStorage.clear();
    },
    setList: function (data) {
        for (var i in data) {
            localStorage.setItem(i, data[i]);
        }
    },
    removeList: function (list) {
        for (var i = 0, len = list.length; i < len; i++) {
            localStorage.removeItem(list[i]);
        }
    }
};

// cookies
let cookie = {
    /**
     * cookie
     * @param cname cookie
     * @param cvalue cookie
     * @param exdays cookie
     */
    setCookie: function (cname, cvalue, exdays) {
        var d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        var expires = "expires=" + d.toUTCString();
        document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
    },
    /**
     * cookie
     * @param cname cookie
     * @returns {null} null
     */
    getCookie: function (cname) {

        var name = cname + "=";
        var decodedCookie = decodeURIComponent(document.cookie);
        var ca = decodedCookie.split(';');
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) == ' ') {
                c = c.substring(1);
            }
            if (c.indexOf(name) == 0) {
                return c.substring(name.length, c.length);
            }
        }
        return null;
    },
}

// 
let referee = {
    // delay
    throttle: (fun, delay) => {
        let last, defertimer;
        return function () {
            let that = this
            let _args = arguments
            let now = +new Date()
            if (last && now < last + delay) {
                clearTimeout(defertimer)
                defertimer = setTimeout(function () {
                    last = now
                    fun.apply(that, _args)
                }, delay)
            } else {
                last = now
                fun.apply(that, _args)
            }
        }
    },
    // 
    debounce: (fun, delay) => {
        return function (args) {
            let that = this
            let _args = args
            clearTimeout(fun.id)
            fun.id = setTimeout(function () {
                fun.call(that, _args)
            }, delay)
        }
    }
}

// 
var Time = {
    //
    getUnix: function () {
        var date = new Date();
        return date.getTime();
    },

    //000
    getTodayUnix: function () {
        var date = new Date();
        date.setHours(0);
        date.setMinutes(0);
        date.setSeconds(0);
        date.setMilliseconds(0);
        return date.getTime();
    },

    //1100
    getYearUnix: function () {
        var date = new Date();
        date.setMonth(0);
        date.setDate(1);
        date.setHours(0);
        date.setMinutes(0);
        date.setSeconds(0);
        date.setMilliseconds(0);
        return date.getTime();
    },
    //
    getLastDate: function (time) {
        var date = new Date(time);
        var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
        var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        return date.getFullYear() + '' + month + '' + day + '';
    },
    //
    getMonthDate: function (time) {
        var date = new Date(time);
        var month = date.getMonth() + 1;
        var day = date.getDate();
        return `${month}${day}`;
    },
    //
    getFormateTime: function (timestamp) {
        var now = this.getUnix();
        var today = this.getTodayUnix();
        //var year = this.getYearUnix();
        var timer = (now - timestamp) / 1000;
        var tip = '';

        if (timer <= 0) {
            tip = '';
        } else if (Math.floor(timer / 60) <= 0) {
            tip = '';
        } else if (timer < 3600) {
            tip = Math.floor(timer / 60) + '';
        } else if (timer >= 3600 && (timestamp - today >= 0)) {
            //tip = Math.floor(timer / 3600) + '';
            let time = new Date(timestamp);
            tip = `${String(time.getHours()).padStart(2, '0')}:${String(time.getMinutes()).padStart(2, '0')}`;
        } else if (timer / 86400 <= 31) {
            tip = Math.ceil(timer / 86400) + '';
        } else if (timer / 86400 <= 365) {
            tip = this.getMonthDate(timestamp);
        } else {
            tip = this.getLastDate(timestamp);
        }
        return tip;
    }

}

export { storage, cookie, referee, Time };