<template>
  <section id="burg">
    <van-list
      v-model="loading"
      :finished="finished"
      @load="selectData"
      finished-text=""
      :error.sync="error"
      error-text=""
    >
      <moments v-for="item in event" :key="item.id" :data="item"></moments>
    </van-list>
  </section>
</template>

<script>
import { List } from "vant";
import moments from "../../../components/moments";
export default {
  components: {
    moments,
    [List.name]: List
  },
  data() {
    return {
      event: [],
      lasttime: "-1",
      loading: false, //
      finished: false, //
      error: false
    };
  },
  computed: {
    userData() {
      return this.$store.state.userData;
    }
  },
  methods: {
    selectData() {
      this.$http
        .get(`event?pagesize=2&lasttime=${this.lasttime}`)
        .then(response => {
          let event = response.data.event;
          this.event.push(...event);
          this.lasttime = this.event[this.event.length - 1].eventTime;
          this.loading = false;
          this.error = false;
        });
    }
  }
};
</script>

<style scoped>
#burg {
  width: 100%;
  height: 100%;
}
</style>